import { baseApi } from '@/api/BaseApi';
export default {
	state: () => ({
		nft: null,
		nftMarketList: null,
		clubs: null,
		standings: null,
		playerInfo: null,
	}),
	mutations: {
		getNft(state, nft)
		{
			// console.log('nft: ', nft);
			state.nft = nft;
		},
		getNftMarketList(state, nftMarketList)
		{
			// console.log('nftMarketList: ', nftMarketList);
			state.nftMarketList = nftMarketList;
		},
		getClubs(state, clubs)
		{
			// console.log('clubs: ', clubs);
			state.clubs = clubs;
		},
		getStandings(state, standings)
		{
			// console.log('standings: ', standings);
			state.standings = standings;
		},
		getPlayerInfo(state, playerInfo)
		{
			// console.log('playerInfo: ', playerInfo);
			state.playerInfo = playerInfo;
		},
	},
	actions: {
		async GET_NFT({ commit, dispatch }, data)
		{
			try
			{
				dispatch('SET_LOADING', true);
				const response = await baseApi.getNftList(data);
				const json = await response.json();
				dispatch('SET_LOADING', false);
				await baseApi.getToken();
				commit('getNft', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		},
		async GET_NFT_NARKET_LIST({ commit, dispatch }, data)
		{
			try
			{
				dispatch('SET_LOADING', true);
				const response = await baseApi.getNftMarketList(data);
				const json = await response.json();
				dispatch('SET_LOADING', false);
				commit('getNftMarketList', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		},
		async GET_CLUB({ commit }, id)
		{
			try
			{
				const response = await baseApi.getClubList(id);
				const json = await response.json();
				commit('getClubs', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		},
		async GET_STANDINGS({ commit })
		{
			try
			{
				const response = await baseApi.getStandings();
				const json = await response.json();
				commit('getStandings', json.response);
			}
			catch (e)
			{
				// console.log('err: ', e);
			};
		},
		async GET_PLAYER_INFO({ commit }, playerId)
		{
			try
			{
				const response = await baseApi.getPlayerInfo(playerId);
				const json = await response.json();
				commit('getPlayerInfo', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		},
	},
	getters: {
		nft (state)
		{
			return state.nft;
		},
		nftMarketList (state)
		{
			return state.nftMarketList;
		},
		clubs (state)
		{
			return state.clubs;
		},
		standings (state)
		{
			return state.standings;
		},
		playerInfo (state)
		{
			return state.playerInfo;
		},
	}
};
