export default {
	state: () => ({
		activeLigueId: 0,
		activeClubId: 0,
		activePosId: ''
	}),
	mutations: {
		setActiveLigueId(state, selectedLigueId)
		{
			state.activeLigueId = selectedLigueId;
		},
		setActiveClubId(state, selectedClubId)
		{
			state.activeClubId = selectedClubId;
		},
		setActivePosId(state, selectedposId)
		{
			state.activePosId = selectedposId;
		}
	},
	actions: {
		SET_ACTIVE_LIGUE_ID({ commit }, selectedLigueId)
		{
			commit('setActiveLigueId', selectedLigueId);
		},
		SET_ACTIVE_CLUB_ID({ commit }, selectedClubId)
		{
			commit('setActiveClubId', selectedClubId);
		},
		SET_ACTIVE_POS_ID({ commit }, selectedposId)
		{
			commit('setActivePosId', selectedposId);
		},
	},
	getters: {
		activeLigueId(state)
		{
			return state.activeLigueId;
		},
		activeClubId(state)
		{
			return state.activeClubId;
		},
		activePosId(state)
		{
			return state.activePosId;
		}
	}
};
