<template>
	<div class="page-content header-clear-medium">
		<div class="content">
			<h1 class="page-title">Your NFT</h1>
			<b-form-select
				v-model="selectedClubId"
				:options="clubs"
				value-field="ID"
				text-field="name"
				class="custom-selector club-selector"
				@change="getCards()"
			>
				<template #first>
					<b-form-select-option :value="0">All clubs</b-form-select-option>
				</template>
			</b-form-select>

			<div class="pos-filter">
				<div class="btn btn-default" :class="[selectedPosId == 'G' ? 'active' : '']"  @click="filterByPos('G')">GK</div>
				<div class="btn btn-default" :class="[selectedPosId == 'D' ? 'active' : '']"  @click="filterByPos('D')">DEF</div>
				<div class="btn btn-default" :class="[selectedPosId == 'M' ? 'active' : '']" @click="filterByPos('M')">MID</div>
				<div class="btn btn-default" :class="[selectedPosId == 'F' ? 'active' : '']"  @click="filterByPos('F')">FW</div>
			</div>

			<PreLoader/>

			<div class="row" v-if="!loading">
				<div class="col-6 pb-4" v-for="item in nft" :key="item.nft_address" @click="showCard(item)">
					<div class="nft-card">
						<div class="nft-card-content">
							<img :src="item.image" class="preload-img img-fluid entered loaded nft-card-image" alt="">
						</div>
					</div>
				</div>
			</div>

			<div class="row mt-5 pb-0 pt-0 justify-content-center" v-if="!loading && nft?.length == 0">
				<div class="col-8 p-0">
					<h2 class="text-center color-theme">
						You dont't have any,<br/> right now.<br/>
						Common get your first
					</h2>
					<a href="/market" class="btn btn-m btn-full mb-3 mt-3 text-uppercase font-900 shadow-s bg-blue-dark">
						Buy NFT cards
					</a>
				</div>
			</div>

			<NftCard
				:selectedCard="selectedCard"
				:fullCardVisible="fullCardVisible"
				:isUserCard="true"
				@closeCard="closeCard"
			/>

		</div>
	</div>
</template>
<script>
import NftCard from './NftCard.vue';
import PreLoader from '@/components/PreLoader.vue';
export default {
	name: 'UserCards',
	components:
	{
		NftCard,
		PreLoader
	},
	data()
	{
		return {
			selectedClubId: 0,
			selectedPosId: '',
			selectedCard: {},
			fullCardVisible: false
		};
	},
	mounted()
	{
		this.getCards();
		this.$store.dispatch('GET_CLUB', this.activeLigueId);
		// this.$store.dispatch('GET_STANDINGS');
		// console.log(this.$store.state.players.player_list);
		// this.nft = this.$store.state.nft;
		// this.standings = this.$store.state.standings;
	},
	methods:
	{
		filterByPos(p)
		{
			this.selectedPosId = p;
			this.getCards();
		},
		getCards()
		{
			this.$store.dispatch('SET_ACTIVE_CLUB_ID', this.selectedClubId);
			this.$store.dispatch('SET_ACTIVE_POS_ID', this.selectedPosId);
			this.$store.dispatch('GET_NFT', { club: this.selectedClubId, leagued: this.activeLigueId, pos: this.selectedPosId });
		},
		showCard (item)
		{
			this.$store.dispatch('GET_PLAYER_INFO', item.player_id);
			this.selectedCard = item;
			this.fullCardVisible = true;
		},
		closeCard()
		{
			this.fullCardVisible = false;
		}
	},
	computed: {
		nft ()
		{
			return this.$store.getters.nft;
		},
		clubs ()
		{
			return this.$store.getters.clubs;
		},
		activeLigueId ()
		{
			return this.$store.getters.activeLigueId;
		},
		loading()
		{
			return this.$store.getters.loading;
		}
	},
	beforeDestroy()
	{
		this.$store.commit('getPlayerInfo', {});
	}
};
</script>

<style lang="scss">
	@import './style.scss';
</style>
