export default {
	state: () => ({
		loading: false,
	}),
	mutations: {
		setLoading(state, loading)
		{
			state.loading = loading;
		}
	},
	actions: {
		SET_LOADING({ commit }, loading)
		{
			commit('setLoading', loading);
		}
	},
	getters: {
		loading(state)
		{
			return state.loading;
		}
	}
};
