export const configureRequest =
{
	baseUrl: process.env.NODE_ENV === 'development' ? '/' : process.env.VUE_APP_BASE_URL,
	initData: process.env.NODE_ENV === 'development' ? 'tester777' : window.Telegram.WebApp.initData,
	getDefaultOptions(additionalHeaders = {})
	{
		const headers = {};
		Object.entries({
			'Content-Type': 'application/json',
			Authorization: this.initData,
			'Cache-Control': 'no-cache',
			...additionalHeaders,
		}).map(([header, value]) =>
		{
			if (typeof value !== 'undefined')
			{
				headers[header] = value;
			};
			return headers;
		});
		return {
			headers,
		};
	},
};
