<template>
	<div class="header">
		<div class="btn btn-default ligue-btn">
			<b-form-select
				v-model="activeLigueId"
				:options="ligues"
				value-field="ID"
				text-field="Name"
				class="ligues-selector"
				@change="getCards()"
			>
				<template #first>
					<b-form-select-option :value="0">All ligues</b-form-select-option>
				</template>
			</b-form-select>
			<img :src="ligue.LogoURL" :alt="ligue.Name" class="logo" v-if="activeLigueId != 0">
			<span v-if="activeLigueId !== 0">{{ ligue.Name }}</span>
			<span v-else>All ligues</span>
		</div>
		<div @click="tonL" dbuttonta-toast="snackbar-cart" class="btn btn-default btn-xxs bg-blue-dark rounded-xs font-500 font-12 hide-long-text wallet-btn">
			{{ showInfo?.showSombols || 'add waletss' }}
		</div>
		<!-- <button @click="send" data-toast="snackbar-cart" class="btn btn-xxs bg-blue-dark rounded-xs font-500 font-12 hide-long-text">send</button> -->
	</div>
</template>
<script>
import { connector } from '@/api/TonConnect';
import { mapGetters } from 'vuex';
export default {
	name: 'BottomNavigation',
	data()
	{
		return {
			tonLink: null,
			universalLink: null,
			source: [{ bridgeUrl: 'https://bridge.tonapi.io/bridge' }],
			dataInfo: null,
			isLink: false,
			walletFlow: null,
			showInfo: null,
			ligue: {},
			// showAllLigues: false
		};
	},
	mounted()
	{
		// this.checkPath();
		this.$store.dispatch('GET_LIGUES');
		this.$store.dispatch('CHANGE_WALLET');
		this.walletFlow = this.$store.state.ton.wallet;
		this.showInfo = this.$store.state.ton;
		// console.log(this.$store.state.ton.wallet);
		// this.test(false);
		// console.log('asdsad', store.getters.getTonLink);
	},
	methods:
	{
		async tonL(event)
		{
			await this.$store.dispatch('GET_TON_LINK');
			this.tonLink = this.$store.state.ton.tonLink;
			await this.test(true);
			console.log(this.universalLink);
			// window.open(this.universalLink);
			window.location.href = this.universalLink;
			// this.tonLink;
			// console.log('tonLink: ', this.$store.state.ton.tonLink);
		},
		async send()
		{
			const neededJson =
			{
				address: this.dataInfo.account.address,
				network: this.dataInfo.account.chain,
				proof:
				{
					timestamp: this.dataInfo.connectItems.tonProof.proof.timestamp,
					domain: this.dataInfo.connectItems.tonProof.proof.domain,
					signature: this.dataInfo.connectItems.tonProof.proof.signature,
					payload: this.dataInfo.connectItems.tonProof.proof.payload,
					state_init: this.dataInfo.account.walletStateInit
				}
			};
			// console.log('neededJson: ', neededJson);
			await this.$store.dispatch('SEND_CONNECT_INFO', neededJson);
		},
		async test(status)
		{
			const walletConnectionSource = {
				universalLink: 'https://app.tonkeeper.com/ton-connect',
				bridgeUrl: 'https://bridge.tonapi.io/bridge'
			};
			// console.log('this.tonLink: ', this.tonLink);
			// console.log('walet: ', walletConnectionSource);
			// console.log('status: ', status);
			this.universalLink = !status
				? await connector.connect(walletConnectionSource)
				: await connector.connect(
					walletConnectionSource,
					{ tonProof: this.tonLink });
			await connector.onStatusChange(
				walletInfo =>
				{
					// console.log('Connection status:', walletInfo);
					this.dataInfo = walletInfo;
					this.send();
				}
			);
			await connector.restoreConnection();
			// unsubscribe;
			// console.log('universalLink: ', this.universalLink, unsubscribe);
		},
		getCards()
		{
			this.$store.dispatch('SET_ACTIVE_CLUB_ID', 0);
			this.$store.dispatch('GET_CLUB', this.activeLigueId);
			this.$store.dispatch('GET_NFT', { club: 0, leagued: this.activeLigueId, pos: this.activePosId });
			this.$store.dispatch('GET_NFT_NARKET_LIST', { club: this.activeClubId, leagued: this.activeLigueId, pos: this.activePosId });
		},
		// checkPath()
		// {
		// 	if (this.$route.name === 'MarketCards')
		// 	{
		// 		this.showAllLigues = false;
		// 		this.$store.dispatch('SET_ACTIVE_LIGUE_ID', this.ligues[0]?.ID);
		// 	}
		// 	else
		// 	{
		// 		this.showAllLigues = true;
		// 		// this.$store.dispatch('SET_ACTIVE_LIGUE_ID', 0);
		// 	}
		// }
	},
	computed:
	{
		ligues ()
		{
			return this.$store.getters.ligues;
		},
		activeClubId ()
		{
			return this.$store.getters.activeClubId;
		},
		activePosId ()
		{
			return this.$store.getters.activePosId;
		},
		activeLigueId: {
			get ()
			{
				return this.$store.getters.activeLigueId;
			},
			set (value)
			{
				this.$store.dispatch('SET_ACTIVE_LIGUE_ID', value);
			}
		},
		...mapGetters([
			'getTonLink'
		])
	},
	watch: {
		// $route (path)
		// {
		// 	this.checkPath();
		// },
		activeLigueId (val)
		{
			this.ligue = this.ligues.filter(function(item)
			{
				return item.ID === val;
			}).pop();
		}
	}
};
</script>
<style lang="scss">
	@import './style.scss';
</style>
